<!--eslint-disable-->
<template>
  <div class="servicos-wrapper">
    <div class="servicos">
      <div
        :id="servico.code"
        class="row"
        v-for="(servico, id) in servicos"
        :key="id"
        :class="{'lateral': servico.order % 2 === 0, 'lateralR': servico.order % 2 !== 0}"
      >
        <div class="wrapper">
          <div class :class="{'right': servico.order % 2 === 0, 'left': servico.order % 2 !== 0}">
            <h2 class="txt" v-html="servico.title"></h2>
            <div class="line"></div>
          </div>
          <div :class="{'left': servico.order % 2 === 0, 'right': servico.order % 2 !== 0}">
            <div class="copy">
              <div class="text" v-html="servico.text"></div>
              <router-link :to="servico.url">
                <div class="btn blue">
                  <div class="txt">Saiba mais</div>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="servicos-mobile">
      <div :id="servico.code" class="row" v-for="(servico, id) in servicos" :key="id">
        <div class="wrapper movement">
          <div class="top">
            <div class="copy">
              <div class="title">{{ servico.title }}</div>
              <div class="line"></div>
              <div class="text" v-html="servico.text"></div>
              <router-link :to="servico.url">
                <div class="btn blue">
                  <div class="txt">Saiba mais</div>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      servicos: [
        {
          id: "",
          code: "",
          title: "",
          text: "",
          url: "",
          order: "",
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getServicos.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var servicos = JSON.parse(result[2]);
        var servicosArray = [];

        map.call(servicos, function (s) {
          servicosArray.push({
            id: s.id,
            code: s.code,
            title: s.title,
            text: s.text,
            url: "/servicos" + s.link,
            order: s.display_order,
          });
        });

        vm.$data.servicos = servicosArray;
      })
      .then(() => {
        vm.FadeIn();
      })
      .then(() => {
        var location = window.location.href;
        var split_url = location.split("/");
        var ref = split_url[split_url.length - 1];

        if (ref.includes("#")) {
          var hash = ref.split("#");

          if ($(".servicos-mobile").css("display") == "flex") {
            var top = $(".servicos-mobile #" + hash[1])[0].offsetTop;

            setTimeout(() => {
              $("html, body").animate({ scrollTop: top - 100 }, 1000);
            }, 500);
          } else {
            var top = $(".servicos #" + hash[1])[0].offsetTop;

            setTimeout(() => {
              $("html, body").animate({ scrollTop: top - 100 }, 1000);
            }, 500);
          }
        }
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.servicos-wrapper {
  display: flex;
}

.servicos {
  height: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;

  @media only screen and (max-width: 991px) {
    display: none;
  }

  .row {
    height: pxToEm(530);
    padding: 0 pxToEm(130);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    &:nth-child(odd) {
      background-color: #fafafa;

      .wrapper {
        max-width: pxToEm(1490);
        width: 100%;
        height: 100%;
        margin: auto;
        position: absolute;

        .left {
          position: absolute;
          width: pxToEm(770);
          // height: auto;
          min-height: pxToEm(200);
          background-color: #fff;
          border-radius: pxToEm(10);
          box-shadow: pxToEm(10) pxToEm(11) pxToEm(17) rgba(0, 0, 0, 0.1);
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          padding: pxToEm(40) pxToEm(60);
          transition: 0.5s;

          .copy {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: pxToEm(200);

            .title {
              font-size: pxToEm(26);
              color: $turquiose;
              font-family: "OS-Regular";
              font-weight: bold;
            }

            .line {
              width: pxToEm(80);
              height: pxToEm(1);
              background-color: $turquiose;
              margin-top: pxToEm(10);
            }

            .text {
              font-size: pxToEm(24);
              color: #707070;
              line-height: 1.5em;
              margin-top: pxToEm(10);
              font-family: "OS-Regular";
              margin-top: pxToEm(20);
            }

            .btn {
              width: pxToEm(131);
              height: pxToEm(44);
              align-self: flex-end;

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                margin-bottom: pxToEm(20);
              }

              .txt {
                font-family: Arial, Helvetica, sans-serif;
                font-size: pxToEm(18);
                font-weight: bold;
              }
            }
          }
        }

        .right {
          width: pxToEm(590);
          height: pxToEm(290);
          background-color: $turquiose;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          padding: 0;
          box-shadow: none;
          border-radius: pxToEm(10);
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          .txt {
            margin: 0;
            font-weight: normal;
            color: $white;
            font-size: pxToEm(28);
            font-family: "OS-Bold";
            text-align: center;
            max-width: 90%;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: #fff;
            margin-top: pxToEm(10);
          }
        }
      }
    }

    &:nth-child(even) {
      background-color: #fff;

      .wrapper {
        max-width: pxToEm(1490);
        width: 100%;
        height: 100%;
        margin: auto;
        position: absolute;

        .left {
          width: pxToEm(590);
          height: pxToEm(290);
          background-color: $turquiose;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          border-radius: pxToEm(10);
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          .txt {
            margin: 0;
            color: $white;
            font-size: pxToEm(28);
            font-family: "OS-Bold";
            text-align: center;
            max-width: 90%;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: #fff;
            margin-top: pxToEm(10);
          }
        }

        .right {
          position: absolute;
          width: pxToEm(770);
          // height: auto;
          min-height: pxToEm(200);
          background-color: #fff;
          border-radius: pxToEm(10);
          box-shadow: pxToEm(10) pxToEm(11) pxToEm(17) rgba(0, 0, 0, 0.1);
          top: 50%;
          transform: translateY(-50%);
          right: 0;
          padding: pxToEm(40) pxToEm(60);
          transition: 0.5s;

          .copy {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: pxToEm(200);

            .title {
              font-size: pxToEm(26);
              color: $turquiose;
              font-family: "OS-Regular";
              font-weight: bold;
            }

            .line {
              width: pxToEm(80);
              height: pxToEm(1);
              background-color: $turquiose;
              margin-top: pxToEm(10);
            }

            .text {
              font-size: pxToEm(24);
              color: #707070;
              line-height: 1.5em;
              margin-top: pxToEm(10);
              font-family: "OS-Regular";
              margin-top: pxToEm(20);
            }

            a {
              align-self: flex-end;
            }

            .btn {
              width: pxToEm(131);
              height: pxToEm(44);
              align-self: flex-end;

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                margin-bottom: pxToEm(20);
              }

              .txt {
                font-family: Arial, Helvetica, sans-serif;
                font-size: pxToEm(18);
                font-weight: bold;
              }
            }
          }
        }
      }
    }
  }
}

.servicos-mobile {
  height: 100%;
  display: none;
  flex-direction: column;
  width: 100%;

  @media only screen and (max-width: 991px) {
    display: flex;
  }

  .row {
    height: auto;
    padding: 0 pxToEm(40);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    // margin-bottom: pxToEm(40);

    @media only screen and (min-width: 600px) and (max-width: 767px) {
      padding: pxToEm(40) pxToEm(20);
    }

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      padding: pxToEm(40) pxToEm(20);
    }

    &:nth-child(even) {
      background-color: #f3f3f3;
    }

    &:nth-child(odd) {
      background-color: #fff;
    }

    .wrapper {
      max-width: pxToEm(1490);
      width: 100%;
      height: 100%;
      margin: auto;
      display: flex;
      flex-direction: column;

      .top {
        width: 100%;
        height: auto;
        transition: 0.5s;
        padding: pxToEm(40) 0;
        display: flex;
        flex-direction: column;

        .copy {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          .title {
            font-size: pxToEm(26);
            color: $turquiose;
            font-family: "OS-Regular";
            font-weight: bold;
            text-align: center;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: $turquiose;
            margin-top: pxToEm(10);
          }

          .text {
            font-size: pxToEm(20);
            color: #707070;
            line-height: 1.5em;
            margin-top: pxToEm(10);
            font-family: "OS-Regular";
            margin-top: pxToEm(20);
            text-align: center;
          }

          .btn {
            width: pxToEm(131);
            height: pxToEm(44);
            margin-top: pxToEm(50);

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              margin-bottom: pxToEm(20);
            }

            .txt {
              font-family: Arial, Helvetica, sans-serif;
              font-size: pxToEm(14);
              font-weight: bold;
            }
          }
        }
      }

      .bottom {
        width: 100%;
        height: auto;
        background-color: $turquiose;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: pxToEm(20) 0;

        .txt {
          color: $white;
          font-size: pxToEm(20);
          font-family: "OS-Bold";
          text-align: center;
        }
      }
    }
  }
}
</style>



